import { type } from "@testing-library/user-event/dist/type";
import { PieChart } from "react-minimal-pie-chart";
import { RandomColor } from "./Colors";



function SkillChart({RawData}) {
    let data = [
        { title: 'One', value: 10, color: '#E38627' },
        { title: 'Two', value: 15, color: '#C13C37' },
        { title: 'Three', value: 20, color: '#6A2135' },
    ];

    if (typeof RawData !== 'undefined' && Array.isArray(RawData)) {
        let total = RawData.reduce((a, b) => a + b.amount, 0);

        data = RawData.map((item) => {
            return {
                title: item.skill,
                value: (item.amount/total)*100,
                color: RandomColor()
            }
        })
    }
    // Display the chart using the PieChart component
    return (
        <div className="chart">
            <PieChart
                data={data}
                label={({ dataEntry }) => dataEntry.title+ " " + dataEntry.value.toFixed(2) + "%"}
                labelStyle={() => ({
                    fontSize: '0.2em',
                    fontFamily: 'sans-serif',
                })}
                radius={30}
                center={[50, 60]}
                viewBoxSize={[150, 100]}
                paddingAngle={3}
                labelPosition={105}
                segmentsShift={(index) => (index === 0 ? 7 : 0.5)}
                animate
                animationDuration={500}
                animationEasing="ease-out"
            />
        </div>
    );
}
export default SkillChart;

