import { useState, useEffect } from 'react'; // to be able to use state and effect
import { useNavigate } from 'react-router-dom'; // to be able to navigate to another page
import SkillChart from './SkillChart'; // to be able to use the skillChart function
import TransChart from './TransChart'; // to be able to use the TransChart function
import './login.css'; // to be able to use the css file
import AuditChart from './AuditChart'; // to be able to use the AuditChart function

// create function to format the skill transactions 
function formatSkillTransactions(transactions) {
    let ret = [];
    for (let i = 0; i < transactions.length; i++) {
        let t = transactions[i];
        let skill = t.type.replace("skill_","");

        if(ret.filter(s => s.skill == skill).length <= 0) {
            ret.push({skill:skill,amount:t.amount})
        }
        else {
            let o = ret.find(s => s.skill === skill)
            o.amount += t.amount
        }
    } 
    return ret; 
}

function Dashboard() {
    // fetch data from API;    
    const token = localStorage.getItem("token");
    const navigate = useNavigate();
    const [userData, setUserData] = useState(null);
    
    const Query = `query {
        user {
          id
          login
          firstName
          lastName
          email
          auditRatio
          totalDown
          totalUp

        }
        transaction(where: { _or: [{ type: { _eq: "xp" } }, { type: { _ilike: "skill%" } }] }) {
          createdAt
          type
          amount
          path
          attrs
        }
      }`;

    useEffect(() => {
        const customFetch = async () => {
            // Fetching the data from the API
            const res = await fetch("https://01.gritlab.ax/api/graphql-engine/v1/graphql",{
                method: "POST",
                headers: {
                "Content-Type": "application/json",
                Authorization: "Bearer " + token,
                },
                body: JSON.stringify({ query: Query }),
            })
            const jsonData = await res.json();
            setUserData(jsonData.data);
        }
        customFetch();
    }, []); 

    if (userData === null) { // if data is not fetched yet, show loading
        return <div>Loading...</div>;
    }

    console.log(userData);


    // Filter transactions based on XP
    let xpTransactions = userData.transaction.filter(t => t.type === "xp" && 
                                                        t.path.includes("school-curriculum") == true && 
                                                        t.path.includes("piscine-js/") == false &&
                                                        t.attrs.hasOwnProperty("auditId") == false) 
    // Filter transactions based on skills
    let skillsTransactions = formatSkillTransactions(userData.transaction.filter(t => t.type.includes("skill") === true));

    console.log(skillsTransactions);

    let skillsString = skillsTransactions.map(s => s.skill).join(", ");

    let xp = xpTransactions.reduce((a, c) => {return a + c.amount},0) // sum of XP transactions 

    xpTransactions.sort((a, b) => new Date(a.createdAt) - new Date(b.createdAt) ) // sort XP transactions by date

    console.log(xpTransactions);
    let u = userData.user[0];
    let auditData = [
        { name: "Received", value: (u.totalDown * 0.000001).toFixed(2) },
        { name: "Done", value: (u.totalUp * 0.000001).toFixed(2) }
      ];

// create a logout function
const logout = () => {
    localStorage.removeItem("token");
    navigate("/");
};

return (
    <div className='main-div'>
      <div id="topDiv"><h1>Dashboard</h1></div>
      <div id="user">
      <h2>User:</h2>
      <p>Login: {u.login}</p>
      <p>First Name: {u.firstName}</p>
      <p>Last Name: {u.lastName}</p>
      <p>Audit Ratio: {u.auditRatio.toFixed(1)}</p>
      <div id= "logoutButton"><button onClick={logout}>Logout</button></div>
      </div>  
      <div id="transactions">
      <h2>Transactions:</h2>
        <p>XP: {(xp*0.001).toFixed(0)}</p>
        <p>Skills: {skillsString}</p>
        <p>Transaction XP per project:</p>
        <div className=".TransChart-wrapper ">
        <div className="TransChart-container">
        <TransChart RawData={xpTransactions} />
        </div> 
        </div>
        </div>
        <h2 id="midDiv"><p>Audit Chart:</p></h2>
        <div id ="barChart"><AuditChart data={auditData}/></div>
        <h2 id="bottomDiv"><p>Skill Chart:</p></h2> 
        <SkillChart RawData={skillsTransactions} />
  </div>
);
}

export default Dashboard;