import axios from "axios";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./login.css";

const getToken = async (userData,password) => {
    let link = `https://01.gritlab.ax/api/auth/signin`
    let response;

    try {
        //login with email  
        if (userData.includes("@")) {
            response = await axios.post(
                link,
                {email : userData, password : password},
                {headers: {Authorization: "Basic " + btoa(userData + ":" + password)}}
            ); 
        } else { // login with username 
            response = await axios.post(
                link,
                {username : userData, password : password},
                {headers: {Authorization: "Basic " + btoa(userData + ":" + password)}}
            ); 
        }
        localStorage.setItem("token", response.data);
    } catch (error) {
        throw error.response.data.error;
    }

}

function LoginPage() {
    const navigate = useNavigate();
    const [userData, setUserData] = useState("");
    const [password, setPassword] = useState("");
    const [errorMessage, setErrorMessage] = useState("");

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
           await getToken(userData,password)
            navigate("/dashboard");
        } catch (error) {
            setErrorMessage(error);
        }
    }

    return (
        <div className="container">
        <h1 id="head1">GraphQL Query</h1>
          <form id="login-form" onSubmit={handleSubmit}>
            <label htmlFor="username">Username:</label>
            <input
              type="text"
              id="username"
              value={userData}
              onChange={(e) => setUserData(e.target.value)}
            />
            <label htmlFor="password">Password:</label>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <button
                className="button"
                type="submit"
            >
              Login
            </button>
            {errorMessage && (
              <p style={{ color: "red", fontWeight: "bold" }}>{errorMessage}</p>
            )}
          </form>
      </div>
    )
}

export default LoginPage;