import React from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';
import { RandomColor } from "./Colors";

const data = [
  { name: 'Jan', uv: 4000, pv: 2400, amt: 2400 },
  { name: 'Feb', uv: 3000, pv: 1398, amt: 2210 },
  { name: 'Mar', uv: 2000, pv: 9800, amt: 2290 },
  { name: 'Apr', uv: 2780, pv: 3908, amt: 2000 },
  { name: 'May', uv: 1890, pv: 4800, amt: 2181 },
  { name: 'Jun', uv: 2390, pv: 3800, amt: 2500 },
  { name: 'Jul', uv: 3490, pv: 4300, amt: 2100 },
];

const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
        let path = payload[0].payload.path.split("/");
        return (
        <div className="custom-tooltip">
<p className="label">{`${path[path.length-1]} ${(payload[0].payload.amount/1000).toFixed(1)}xp`}</p> </div>
        );
    }
  
    return null;
};

const LineChartComponent = ({RawData}) => {
  return (
    <LineChart width={1200} height={400} data={RawData}>
      <CartesianGrid strokeDasharray="3 3" />
      <XAxis dataKey="name" />
      <YAxis />
      <Tooltip content={<CustomTooltip />} />
      <Legend />
      <Line type="monotone" dataKey="amount" name="Amount" stroke={RandomColor()} activeDot={{ r: 8 }} />
    </LineChart>
  );
};

export default LineChartComponent;
